<template>
  <div id="app">
    <div class="header">
      <img src="paimon_1.gif" alt="GIF" class="header-gif" />
      <h1>Genshin Impact Team Generator</h1>
    </div>

    
    <div class="team">
      <div v-for="(character, index) in team" :key="index"
           class="card">
        <div v-if="character">
          <h2>{{ character.name }}</h2>
          <img :src="characterImageUrl(character)" :alt="character.name" 
          :class="[character ? `rarity-${character.rarity}` : '']"/>
        </div>
        <div v-else>
          <h2>No Character</h2>
          <img src="paimon2.png" alt="No character" />
        </div>
        <button class="button smallButton" @click="getRandomCharacter(index)">Regenerate</button>
      </div>
    </div>
    
    <button class="button" @click="generateNewTeam">Generate New Team</button>
    <div class="header">
      <img src="paimon_2.gif" alt="GIF" class="header-gif" />
      <h1>Boss Generator</h1>
    </div>

    <div class="boss-container">
      <div class="boss">
        <div class="boss-content">
          <div v-if="boss">
            <h2>Boss: {{ boss.name }}</h2>
            <img class="bossImg" :src="characterImageUrl(boss)" :alt="boss.name" />
          </div>
          <div v-else>
            <h2>No Boss</h2>
            <img src="paimon2.png" alt="No character" />
          </div>
        </div>
        <button class="button" @click="getRandomBoss">Generate</button>
      </div>
    </div>

  </div>

</template>

<script>
import charactersData from './assets/characters.json';
import bossesData from './assets/bosses.json';

export default {
  data() {
    return {
      characters: [],
      team: [null, null, null, null],
      boss: null,
     };
    
  },
  created() {
    this.characters = charactersData;
  },
  methods: {
    getRandomCharacter(index) {
    const availableCharacters = this.characters.filter(character => 
      !this.team.includes(character)
    );

    if (availableCharacters.length === 0) {
      alert("No more unique characters to pick.");
      return;
    }

    const randomIndex = Math.floor(Math.random() * availableCharacters.length);
    this.team.splice(index, 1, availableCharacters[randomIndex]);
  },getRandomBoss(){
    if (bossesData.length === 0) {
      alert("No bosses available.");
      return;
    }

    const randomIndex = Math.floor(Math.random() * bossesData.length);
    this.boss = bossesData[randomIndex];
  
  },
    generateNewTeam() {
      for(let i =0; i < 4; i++){
        this.getRandomCharacter(i);
      }
    },
    characterImageUrl(character) {
      return require(`./assets/${character.image}`);
    },
  },
  computed: {
    teamComplete() {
      return this.team.every(character => character !== null);
    },
  },
};
</script>

